import { Component, Input } from '@angular/core';
import { get } from 'lodash-es';
import { IManagedObject } from '@c8y/client';
import { gettext } from '@c8y/ngx-components';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'c8y-confirm-device-profile-selection-step',
  templateUrl: 'confirm-device-profile-selection-step.component.html'
})
export class ConfirmDeviceProfileSelectionStepComponent {
  @Input() selectedDeviceProfile: IManagedObject;
  readonly DEVICE_TYPE_NOT_DEFINED = gettext('Device type not defined');
  constructor(
    private translate: TranslateService
  ) {}

  get softwares() {
    return get(this.selectedDeviceProfile, ['c8y_DeviceProfile', 'software'], []);
  }

  get configurations() {
    return get(this.selectedDeviceProfile, ['c8y_DeviceProfile', 'configuration'], []);
  }

  getDeviceTypeTitle(deviceProfile: IManagedObject): string {
    return get(
      deviceProfile,
      'c8y_Filter.type',
      this.translate.instant(this.DEVICE_TYPE_NOT_DEFINED));
  }
}
