import {
  Component,
  EventEmitter,
  Input,
  OnChanges,
  OnInit,
  Output,
  SimpleChanges,
  ViewChild
} from '@angular/core';
import { IOperationBulk, OperationBulkGeneralStatus } from '@c8y/client';
import {
  AlertService,
  gettext,
  ListItemComponent,
  ModalService,
  Status
} from '@c8y/ngx-components';
import { assign, cloneDeep } from 'lodash-es';
import { BsModalRef, BsModalService, ModalOptions } from 'ngx-bootstrap/modal';
import { BulkOperationsService } from '../bulk-operations.service';
import { BulkOperationsRescheduleModalComponent } from '../modal/bulk-operations-reschedule-modal.component';
import { BULK_OPERATION_STATUS_OPTIONS } from './bulk-operation-list-item.model';

@Component({
  selector: 'c8y-bulk-operation-list-item',
  templateUrl: './bulk-operation-list-item.component.html'
})
export class BulkOperationListItemComponent implements OnInit, OnChanges {
  @Input()
  bulkOperation: Partial<IOperationBulk>;
  @Input()
  detailsCollapsed: boolean = true;
  @Input()
  readOnly: boolean = false;
  @Output() showFailedOperation = new EventEmitter();
  @Output() reload: EventEmitter<any> = new EventEmitter();
  @ViewChild('listItem', { static: true }) listItem: ListItemComponent;
  iconClass: string;
  finishDate: number;
  refreshLoading = false;
  bulkOperationGeneralStatus = OperationBulkGeneralStatus;
  bulkOperationStatusOptions = BULK_OPERATION_STATUS_OPTIONS;
  bsModalRef: BsModalRef;
  finishDatePopoverText: string = gettext(
    'Approximate date, estimated based on the bulk operation settings.'
  );
  progressBarClass: string = 'progress';
  progressBarColor: string;
  progressBarStatus: number = 0;

  constructor(
    private bulkOperationsService: BulkOperationsService,
    private modal: ModalService,
    private alert: AlertService,
    private bsModalService: BsModalService
  ) {}

  ngOnInit() {
    this.iconClass = this.getIconClass();
    this.finishDate = this.calculateFinishDateMs();
    this.setProgressBar();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.bulkOperation && !changes.bulkOperation.firstChange) {
      this.iconClass = this.getIconClass();
      this.setProgressBar();
    }
  }

  getIconClass() {
    return this.bulkOperationStatusOptions.find(statusOption =>
      statusOption.generalStatus.includes(this.bulkOperation.generalStatus)
    ).iconClass;
  }

  calculateFinishDateMs(): number {
    const startDateMs: number = new Date(this.bulkOperation.startDate).getTime();
    const creationRampMs: number = this.bulkOperation.creationRamp * 1000;

    return startDateMs + creationRampMs * this.bulkOperation.progress.all;
  }

  progressBarProgressFn() {
    return (
      ((this.bulkOperation.progress.successful + this.bulkOperation.progress.failed) /
        this.bulkOperation.progress.all) *
      100
    );
  }

  setProgressBar() {
    const staticContentOfClass = 'progress-bar progress-striped active progress-bar';

    const progressBarState = {
      EXECUTING: {
        progressBarClass: 'progress progress-striped active',
        progressBarColor: `${staticContentOfClass}-primary`,
        progressBarStatus: this.progressBarProgressFn()
      },
      EXECUTING_WITH_ERROR: {
        progressBarClass: 'progress progress-striped active',
        progressBarColor: `${staticContentOfClass}-danger`,
        progressBarStatus: this.progressBarProgressFn()
      },
      FAILED: {
        progressBarClass: 'progress',
        progressBarColor: `${staticContentOfClass}-danger`,
        progressBarStatus: 100
      },
      SUCCESSFUL: {
        progressBarClass: 'progress',
        progressBarColor: `${staticContentOfClass}-success`,
        progressBarStatus: 100
      }
    };

    assign(this, progressBarState[this.bulkOperation.generalStatus]);
  }

  editSchedule() {
    const rescheduledOperation = cloneDeep(this.bulkOperation);
    const initialState = {
      bulkOperation: rescheduledOperation
    };
    const modalOptions = { initialState, class: 'modal-sm', backdrop: 'static' } as ModalOptions;
    this.bsModalRef = this.bsModalService.show(
      BulkOperationsRescheduleModalComponent,
      modalOptions
    );
  }

  async cancelBulkOperation() {
    try {
      await this.modal.confirm(
        gettext('Cancel bulk operation'),
        gettext('You are about to cancel the bulk operation. Do you want to proceed?'),
        Status.DANGER
      );
      await this.bulkOperationsService.deleteBulkOperation(this.bulkOperation.id);
      this.reload.emit();
      this.alert.success(gettext('Operation canceled.'));
    } catch (er) {
      this.alert.addServerFailure(er);
    }
  }

  retryFailedOperation() {
    const clonedBulk = cloneDeep(this.bulkOperation);

    // change the id to failedparentId similar to the logic in deviceBulkControl.service.js
    delete clonedBulk.groupId;
    clonedBulk.failedParentId = this.bulkOperation.id;

    // show reschdedule modal:
    const initialState = {
      bulkOperation: clonedBulk,
      isRetryOperation: true
    };
    const modalOptions = { initialState, class: 'modal-sm', backdrop: 'static' } as ModalOptions;
    this.bsModalRef = this.bsModalService.show(
      BulkOperationsRescheduleModalComponent,
      modalOptions
    );
    this.bsModalRef.content.closeSubject.subscribe(() => {
      this.reload.emit();
    });
  }

  async setToSuccessful() {
    try {
      await this.modal.confirm(
        gettext('Set manually bulk operation to successful'),
        gettext(
          'You are about to change the bulk operation status to successful. Do you want to proceed?'
        ),
        Status.DANGER
      );

      await this.bulkOperationsService.updateBulkOperation({
        id: this.bulkOperation.id,
        generalStatus: OperationBulkGeneralStatus.SUCCESSFUL
      });
      this.reload.emit();
      this.alert.success(gettext('Operation status changed to successful.'));
    } catch (er) {
      this.alert.addServerFailure(er);
    }
  }

  openFailedOperation(failedParentId) {
    this.showFailedOperation.emit(failedParentId);
  }

  isStatusScheduled() {
    return this.bulkOperation.generalStatus === this.bulkOperationGeneralStatus.SCHEDULED;
  }

  isStatusExecutingOrExecutingWithError() {
    return (
      this.bulkOperation.generalStatus === this.bulkOperationGeneralStatus.EXECUTING ||
      this.bulkOperation.generalStatus === this.bulkOperationGeneralStatus.EXECUTING_WITH_ERROR
    );
  }

  isStatusCanceled() {
    return this.bulkOperation.generalStatus === this.bulkOperationGeneralStatus.CANCELED;
  }

  isStatusFailed() {
    return this.bulkOperation.generalStatus === this.bulkOperationGeneralStatus.FAILED;
  }
}
