import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { IOperation } from '@c8y/client';
import { get } from 'lodash-es';
import { BaseStepperComponent } from '../base-stepper.component';
import { BulkOperationsService } from '../bulk-operations.service';
import { OperationDetails } from '../operation-details.model';

@Component({
  selector: 'bulk-operation-from-single',
  templateUrl: 'bulk-operation-from-single.component.html'
})
export class BulkOperationFromSingleComponent extends BaseStepperComponent implements OnInit {
  constructor(
    private activatedRoute: ActivatedRoute,
    private bulkOperationService: BulkOperationsService
  ) {
    super();
  }

  protected async retrieveOperationPrototype(): Promise<OperationDetails> {
    const prototype: IOperation = (await this.bulkOperationService.getOperation(
      this.activatedRoute.snapshot.params.operationId
    )).data;

    return {
      description: get(prototype, 'description'),
      prototype
    };
  }
}
