import { Component, EventEmitter, Input, Output } from '@angular/core';
import { IManagedObject, IOperationBulk } from '@c8y/client';

@Component({
  selector: 'c8y-bulk-operation-details',
  templateUrl: './bulk-operation-details.component.html'
})
export class BulkOperationDetailsComponent {
  allFilterIsSelected: boolean = false;
  collapsed: boolean;
  device: IManagedObject;

  @Input()
  bulkOperation: Partial<IOperationBulk>;
  @Input()
  readOnly: boolean = false;
  @Output() showFailedOperation: EventEmitter<number> = new EventEmitter();

  scrollToRetriedFromOperation(failedOperationId) {
    this.showFailedOperation.emit(failedOperationId);
  }
}
