import { Component, Output, EventEmitter, Input } from '@angular/core';
import { Observable, of } from 'rxjs';
import {
  shareReplay,
  map,
  switchMap,
  distinctUntilKeyChanged,
  distinctUntilChanged,
  catchError,
  tap
} from 'rxjs/operators';
import { IManagedObject, IResultList, InventoryService } from '@c8y/client';
import { memoize } from '@c8y/ngx-components';
import { RepositoryService, FirmwareBinary } from '@c8y/ngx-components/repository';
import { BulkOperationsService } from '../../bulk-operations.service';
import { property } from 'lodash-es';

@Component({
  selector: 'c8y-version-or-patch-step',
  templateUrl: 'version-or-patch.component.html'
})
export class VersionOrPatchComponent {
  @Input() selected: IManagedObject;
  @Output() versionOrPatch: EventEmitter<IManagedObject> = new EventEmitter<IManagedObject>();

  elementCount: number = 0;
  DD_LOW_COUNT: number = 10;

  firmware$: Observable<IManagedObject> = this.bulkOpsService.firmwareId.pipe(
    distinctUntilChanged(),
    switchMap( id => this.inventoryService.detail$(id).pipe( catchError(error => of(error)) ) ),
    shareReplay(1)
  );

  baseVersions$: Observable<IResultList<IManagedObject>> = this.firmware$.pipe(
    distinctUntilKeyChanged('id'),
    switchMap(firmware => this.repositoryService.listBaseVersions(firmware)),
    tap(resp => {
      this.elementCount = resp.data ? resp.data.length : 0;
    }),
    shareReplay(1)
  );

  isLegacy$: Observable<boolean> = this.firmware$.pipe(
    map(firmware => this.repositoryService.isLegacyEntry(firmware)),
    shareReplay(1)
  );

  expanded: { [id: string]: boolean } = {};

  constructor(
    private repositoryService: RepositoryService,
    private inventoryService: InventoryService,
    private bulkOpsService: BulkOperationsService
  ) {
    this.DD_LOW_COUNT = this.bulkOpsService.DD_LOW_COUNT;
  }

  @memoize()
  getBinaryName$(binaryUrl) {
    return this.repositoryService.getBinaryName$(binaryUrl);
  }

  @memoize(property('id'))
  getBaseVersionAndPatches$(baseVersion) {
    return this.firmware$.pipe(
      distinctUntilKeyChanged('id'),
      switchMap(firmware =>
        this.repositoryService.listBaseVersionAndPatches(firmware, baseVersion)
      ),
      shareReplay(1)
    );
  }

  @memoize(property('id'))
  getPatchVersionsCount$(baseVersion: FirmwareBinary) {
    return this.firmware$.pipe(
      distinctUntilKeyChanged('id'),
      switchMap(() => this.firmware$),
      switchMap(firmware => this.repositoryService.getPatchVersionsCount$(firmware, baseVersion)),
      shareReplay(1)
    );
  }

  selectVersionOrPatch(selected: boolean, versionOrPatch: IManagedObject) {
    if (selected) {
      this.versionOrPatch.emit(versionOrPatch);
    }
  }
}
