import { Component, ViewContainerRef, ViewChild, ComponentFactoryResolver } from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap';
import { OperationType } from '../list/type-list/operation-type.model';

@Component({
  selector: 'c8y-bulk-operations-modal',
  templateUrl: 'bulk-operations-modal.component.html'
})
export class BulkOperationsModalComponent {
  @ViewChild('container', { static: false, read: ViewContainerRef }) container;
  showComponent: boolean = false;
  constructor(private bsModalRef: BsModalRef, private resolver: ComponentFactoryResolver) {}

  cancel() {
    this.bsModalRef.hide();
  }

  createComponent(item: OperationType) {
    if (item && item.component) {
      this.showComponent = true;
      const factory = this.resolver.resolveComponentFactory(item.component);
      this.container.createComponent(factory);
    }
  }
}
