import { Component, ElementRef, Input, ViewChild } from '@angular/core';
import { IOperation } from '@c8y/client';
import { AlertService, gettext } from '@c8y/ngx-components';
import { BulkOperationsService, C8Y_BULK_TYPE_FRAGMENTS } from '../bulk-operations.service';
import { omit } from 'lodash-es';
import { SINGLE_OP_STATUS_NAMES, SINGLE_OP_STATUS_PROPERTIES } from './single-operation-item.model';

@Component({
  selector: 'c8y-single-operation-item',
  templateUrl: './single-operation-item.component.html'
})
export class SingleOperationItemComponent {
  device = {};
  showIfTextTruncated = false;
  singleOperationStatusNames = SINGLE_OP_STATUS_NAMES;
  singleOperationsStatusProperties = SINGLE_OP_STATUS_PROPERTIES;
  iconClass: string;
  @Input()
  operation: IOperation;
  @Input()
  readOnly: boolean = false;
  @ViewChild('failureReason', { static: true }) failureReasonRef: ElementRef;

  constructor(
    private bulkOperationsService: BulkOperationsService,
    private alertService: AlertService
  ) {}

  async ngOnInit() {
    this.iconClass = this.getIconClass();
    this.device = await this.getDeviceMO(this.operation.deviceId);
  }

  ngAfterViewInit() {
    this.isTextTruncated();
  }

  async retrySingleOperation(singleOperation) {
    const operationToCreate = omit(singleOperation, [
      'id',
      'self',
      'status',
      'failureReason',
      'creationTime',
      'delivery'
    ]);

    try {
      await this.bulkOperationsService.createSingleOperation(operationToCreate);
      this.alertService.success(gettext('Operation created.'));
    } catch (er) {
      this.alertService.addServerFailure(er);
    }
  }

  getIconClass() {
    return this.singleOperationsStatusProperties.find(
      operationProperties => operationProperties.label.toUpperCase() === this.operation.status
    ).iconClass;
  }

  private async getDeviceMO(deviceId) {
    const inventoryMO = await this.bulkOperationsService.getManagedObject(deviceId);
    if (inventoryMO) {
      return inventoryMO.data;
    }
  }

  private isTextTruncated() {
    if (this.operation.failureReason) {
      const el = this.failureReasonRef.nativeElement;
      this.showIfTextTruncated = el.offsetWidth < el.scrollWidth ? true : false;
    }
  }
}
