import { Injectable } from '@angular/core';
import { IOperationBulk } from '@c8y/client';
import { BsModalService } from 'ngx-bootstrap/modal';
import { BulkOperationsService } from '../bulk-operations.service';
import { BulkOperationWrapperComponent } from './bulk-operation-wrapper.component';

@Injectable()
export class BulkOperationDetailsService {
  constructor(
    private bulkOperationsService: BulkOperationsService,
    private modalService: BsModalService
  ) {}

  async openDetails(bulkOperationId) {
    const bulkOperation: IOperationBulk = (await this.bulkOperationsService.getBulkOperationById(
      bulkOperationId
    )).data;
    const initialState = { bulkOperation };
    this.modalService.show(BulkOperationWrapperComponent, {
      initialState,
      class: 'modal-lg'
    });
  }
}
