import { CdkStep } from '@angular/cdk/stepper';
import { Component } from '@angular/core';
import { IManagedObject, IOperation, IResultList } from '@c8y/client';
import { C8yStepper, gettext } from '@c8y/ngx-components';
import { RepositoryService, RepositoryType } from '@c8y/ngx-components/repository';
import { TranslateService } from '@ngx-translate/core';
import { get, isEqual, uniqWith } from 'lodash-es';
import { BehaviorSubject, combineLatest, from, Observable, Subscription } from 'rxjs';
import { debounceTime, distinctUntilChanged, shareReplay, switchMap } from 'rxjs/operators';
import { BaseStepperComponent } from '../base-stepper.component';
import { BulkOperationsService } from '../bulk-operations.service';
import { OperationDetails } from '../operation-details.model';

@Component({
  selector: 'c8y-stepper-bulk-type-configuration',
  templateUrl: 'stepper-bulk-type-configuration.component.html'
})
export class StepperBulkTypeConfiguration extends BaseStepperComponent {
  readonly NO_DEVICE_TYPE_AVAILABLE = gettext('Undefined`device type`');
  selectedConfiguration: IManagedObject;
  elementCount: number = 0;
  DD_LOW_COUNT: number = 10;
  textFilter$: BehaviorSubject<string> = new BehaviorSubject('');
  configType$: BehaviorSubject<string> = new BehaviorSubject('');
  configTypes = [];
  selectedConfigType = { name: '' };
  configurations$: Observable<IResultList<IManagedObject>> = combineLatest(
    this.textFilter$,
    this.configType$
  ).pipe(
    switchMap(([name, configType]) => this.getConfiguration(name, configType)),
    shareReplay(1)
  );
  private configTypeSubscription: Subscription;

  constructor(
    private bulkOperationService: BulkOperationsService,
    private repositoryService: RepositoryService,
    private translate: TranslateService
  ) {
    super();
    this.DD_LOW_COUNT = this.bulkOperationService.DD_LOW_COUNT;
    this.loadConfigurationTypes();
  }

  loadConfigurationTypes() {
    this.configTypeSubscription = this.configType$
      .pipe(
        debounceTime(300),
        distinctUntilChanged(),
        switchMap(searchStr => {
          const query = { configurationType: `*${searchStr}*` };
          return from(
            this.repositoryService.listRepositoryEntries(RepositoryType.CONFIGURATION, { query })
          );
        })
      )
      .subscribe(result => {
        const { data } = result;
        this.configTypes = uniqWith(data.map(val => ({ name: val.configurationType })), isEqual);
      });
  }
  ngOnDestroy(): void {
    this.configTypeSubscription.unsubscribe();
  }

  selectConfiguration(configuration: IManagedObject) {
    this.selectedConfiguration = configuration;
  }

  goToSecondStep($event: { stepper: C8yStepper; step: CdkStep }) {
    $event.stepper.next();
    this.getConfigBinary();
    this.deviceTypes = this.selectedConfiguration.deviceType;
  }

  async getConfigBinary() {
    if (this.selectedConfiguration.url) {
      this.selectedConfiguration.binary = await this.repositoryService.getBinaryText(
        this.selectedConfiguration.url,
        { allowExternal: true }
      );
    }
  }

  getDeviceTypeTitle(configuration: IManagedObject): string {
    return get(configuration, 'deviceType', this.translate.instant(this.NO_DEVICE_TYPE_AVAILABLE));
  }

  protected retrieveOperationPrototype(): OperationDetails {
    const configuration = {
      type: this.selectedConfiguration.configurationType,
      url: this.selectedConfiguration.url
    };

    return {
      name: gettext('Configuration update'),
      description: get(this.selectedConfiguration, 'name'),
      prototype: ({
        description: `Update configuration to: ${this.selectedConfiguration.name}.`,
        c8y_DownloadConfigFile: configuration
      } as unknown) as IOperation
    };
  }

  private getConfiguration(name?: string, configurationType?: string) {
    const query: any = name ? { name: `*${name}*` } : {};
    if (configurationType) {
      query.__or = [{ configurationType }, { __not: { __has: `configurationType` } }];
    }
    return this.repositoryService.listRepositoryEntries(RepositoryType.CONFIGURATION, { query });
  }
}
