import { Component, Output, EventEmitter } from '@angular/core';
import { Observable, BehaviorSubject, combineLatest, Subscription, from } from 'rxjs';
import { switchMap, shareReplay, debounceTime, distinctUntilChanged, tap } from 'rxjs/operators';
import { IResultList, IManagedObject } from '@c8y/client';
import { property, isEqual, uniqWith, get } from 'lodash-es';
import { gettext } from '@c8y/ngx-components';
import { TranslateService } from '@ngx-translate/core';
import { DeviceProfileService } from '@c8y/ngx-components/device-profile';

@Component({
  selector: 'c8y-select-device-profile-step',
  templateUrl: 'select-device-profile-step.component.html'
})
export class SelectDeviceProfileStepComponent {
  @Output() deviceProfile: EventEmitter<IManagedObject> = new EventEmitter<IManagedObject>();
  textFilter$: BehaviorSubject<string> = new BehaviorSubject('');
  deviceType$: BehaviorSubject<string> = new BehaviorSubject('');
  readonly DEVICE_TYPE_NOT_DEFINED = gettext('Device type not defined');
  deviceTypes = [];
  selectedDeviceType = { name: '' };
  deviceProfile$: Observable<IResultList<IManagedObject>> = combineLatest(
    this.textFilter$,
    this.deviceType$
  ).pipe(
    switchMap(([name, deviceType]) => this.getDeviceProfiles(name, deviceType)),
    shareReplay(1)
  );
  private deviceTypeSubscription: Subscription;

  constructor(
    private deviceProfileService: DeviceProfileService,
    private translate: TranslateService
  ) {
    this.loadDeviceTypes();
  }

  loadDeviceTypes() {
    this.deviceTypeSubscription = this.deviceType$
      .pipe(
        debounceTime(300),
        distinctUntilChanged(),
        switchMap(searchStr => {
          const query = { 'c8y_Filter.type': `*${searchStr}*` };
          return from(this.deviceProfileService.getDeviceProfiles(query));
        })
      )
      .subscribe( ({ data }) => {
        this.deviceTypes = uniqWith(
          data.map(val => ({ name: val.c8y_Filter.type })).filter(val => val.name),
          isEqual
        );
      });
  }

  ngOnDestroy() {
    this.deviceTypeSubscription.unsubscribe();
  }

  selectDeviceProfile(deviceProfile: IManagedObject) {
    this.deviceProfile.emit(deviceProfile);
  }

  getDeviceTypeTitle(deviceProfile: IManagedObject): string {
    return get(
      deviceProfile,
      'c8y_Filter.type',
      this.translate.instant(this.DEVICE_TYPE_NOT_DEFINED));
  }

  private getDeviceProfiles(name?: string, deviceType?: string) {
    const query: any = deviceType ? { 'c8y_Filter.type': deviceType } : {};
    if (name) {
      query.name = `*${name}*`;
    }
    return this.deviceProfileService.getDeviceProfiles(query);
  }

}
