import { CdkStep } from '@angular/cdk/stepper';
import { Component, ViewChild } from '@angular/core';
import { IOperation } from '@c8y/client';
import { C8yStepper, gettext, ModalService, Status } from '@c8y/ngx-components';
import { TranslateService } from '@ngx-translate/core';
import { uniq } from 'lodash-es';
import { BaseStepperComponent } from '../base-stepper.component';
import { OperationDetails } from '../operation-details.model';
import { SelectSoftwareStepComponent } from './software-update-stepper/select-software-step.component';
import {
  ISelectedSoftware,
  ISoftwareUpdateOperationPrototype
} from './software-update-stepper/select-software.model';

@Component({
  selector: 'c8y-stepper-bulk-type-software',
  templateUrl: 'stepper-bulk-type-software.component.html'
})
export class StepperBulkTypeSoftware extends BaseStepperComponent {
  readonly descriptionTemplateSingle: string = gettext(
    'Update software to: {{ name }} (version {{ version }})'
  );
  readonly descriptionTemplateOneOther: string = gettext(
    'Update software to: {{ name }} (version {{ version }}) and one other'
  );
  readonly descriptionTemplateMultiple: string = gettext(
    'Update software to: {{ name }} (version {{ version }}) and {{ count }} others'
  );

  selectedSoftware: ISelectedSoftware[] = [];
  @ViewChild(SelectSoftwareStepComponent, { static: false })
  selectSoftware: SelectSoftwareStepComponent;

  constructor(private modal: ModalService, private translate: TranslateService) {
    super();
  }

  onSoftwareSelected(selectedItem) {
    this.selectedSoftware = this.selectedSoftware.filter(
      item => item.software.id !== selectedItem.software.id
    );
    this.selectedSoftware.push(selectedItem);
  }

  async confirmSoftwareSelection($event: { stepper: C8yStepper; step: CdkStep }) {
    const deviceTypes: string[] = this.getUniqueDeviceTypes();
    this.deviceTypes = deviceTypes;
    if (deviceTypes.length > 1) {
      try {
        await this.modal.confirm(
          gettext('Selected software for various device types'),
          gettext('Operation may fail due to unsupported software. Do you want to proceed?'),
          Status.WARNING,
          { ok: gettext('Confirm'), cancel: gettext('Cancel') }
        );
        $event.stepper.next();
      } catch (ex) {
        this.selectedSoftware = [];
        this.selectSoftware.resetSelection();
      }
    } else {
      $event.stepper.next();
    }
  }

  protected retrieveOperationPrototype(): OperationDetails {
    const softwareList: ISoftwareUpdateOperationPrototype[] = this.selectedSoftware.map(item => ({
      name: item.software.name,
      version: item.version.c8y_Software.version,
      url: item.version.c8y_Software.url,
      action: item.action
    }));

    const interpolationParams: object = {
      name: softwareList[0].name,
      version: softwareList[0].version,
      count: softwareList.length - 1
    };
    let description: string;
    switch (softwareList.length) {
      case 1:
        description = this.translate.instant(this.descriptionTemplateSingle, interpolationParams);
        break;
      case 2:
        description = this.translate.instant(this.descriptionTemplateOneOther, interpolationParams);
        break;
      default:
        description = this.translate.instant(this.descriptionTemplateMultiple, interpolationParams);
    }

    return {
      name: gettext('Software update'),
      prototype: ({
        description,
        c8y_SoftwareUpdate: softwareList
      } as unknown) as IOperation
    };
  }

  private getUniqueDeviceTypes(): string[] {
    return uniq(
      this.selectedSoftware
        .map(item => item.software.c8y_Filter && item.software.c8y_Filter.type)
        .filter(type => !!type)
    );
  }
}
