import { Component, Output, EventEmitter, Input } from '@angular/core';
import { Observable, BehaviorSubject } from 'rxjs';
import { debounceTime, distinctUntilChanged, tap, switchMap, shareReplay } from 'rxjs/operators';
import { memoize } from '@c8y/ngx-components';
import { RepositoryService, RepositoryType } from '@c8y/ngx-components/repository';
import { gettext } from '@c8y/ngx-components';
import { IResultList, IManagedObject } from '@c8y/client';
import { TranslateService } from '@ngx-translate/core';
import { property, get } from 'lodash-es';
import { BulkOperationsService } from '../../bulk-operations.service';

@Component({
  selector: 'c8y-select-firmware-step',
  templateUrl: 'select-firmware.component.html'
})
export class SelectFirmwareComponent {
  @Input() selected: IManagedObject;
  @Output() firmware: EventEmitter<IManagedObject> = new EventEmitter<IManagedObject>();
  elementCount: number = 0;
  DD_LOW_COUNT: number = 10;
  textFilter$: BehaviorSubject<string> = new BehaviorSubject('');

  firmwares$: Observable<IResultList<IManagedObject>> = this.textFilter$
    .pipe(
      debounceTime(400),
      distinctUntilChanged()
    )
    .pipe(
      switchMap(text => this.getFirmwares(text)),
      tap(resp => {
        this.elementCount = resp.data ? resp.data.length : 0;
      }),
      shareReplay(1)
    );
  isLegacy = this.repositoryService.isLegacyEntry.bind(this.repositoryService);
  readonly DEVICE_TYPE_NOT_AVAILABLE: string = gettext('No device type available');
  readonly DESCRIPTION_NOT_AVAILABLE: string = gettext('No description available');

  constructor(
    private repositoryService: RepositoryService,
    private bulkOpsService: BulkOperationsService,
    private translate: TranslateService
  ) {}

  ngOnInit() {
    this.DD_LOW_COUNT = this.bulkOpsService.DD_LOW_COUNT;
  }

  selectFirmware(selected: boolean, fw: IManagedObject) {
    if (selected) {
      this.firmware.emit(fw);
    }
  }

  @memoize(property('id'))
  getBaseVersionsCount$(firmware: IManagedObject): Observable<number | string> {
    return this.firmwares$.pipe(
      switchMap(() => this.repositoryService.getBaseVersionsCount$(firmware)),
      shareReplay(1)
    );
  }

  getDescriptionTitle(firmware: IManagedObject): string {
    return get(
      firmware,
      'description',
      this.translate.instant(this.DESCRIPTION_NOT_AVAILABLE));
  }

  getDeviceTypeTitle(firmware: IManagedObject): string {
    return get(
      firmware,
      'c8y_Filter.type',
      this.translate.instant(this.DEVICE_TYPE_NOT_AVAILABLE));
  }

  private getFirmwares(partialName?: string) {
    return this.repositoryService.listRepositoryEntries(RepositoryType.FIRMWARE, { partialName });
  }

}
