import { Component, EventEmitter, Output, ViewChildren, QueryList } from '@angular/core';
import { IManagedObject, IResultList } from '@c8y/client';
import { gettext, memoize, ListItemRadioComponent } from '@c8y/ngx-components';
import { RepositoryService, RepositoryType } from '@c8y/ngx-components/repository';
import { isEqual, property, uniqWith, get } from 'lodash-es';
import { BehaviorSubject, combineLatest, from, Observable, Subscription } from 'rxjs';
import { debounceTime, distinctUntilChanged, shareReplay, switchMap } from 'rxjs/operators';
import { ISelectedSoftware } from './select-software.model';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'c8y-select-software-step',
  templateUrl: 'select-software-step.component.html'
})
export class SelectSoftwareStepComponent {
  @Output() software: EventEmitter<ISelectedSoftware> = new EventEmitter<ISelectedSoftware>();
  textFilter$: BehaviorSubject<string> = new BehaviorSubject('');
  deviceType$: BehaviorSubject<string> = new BehaviorSubject('');
  deviceTypes = [];
  selectedDeviceType = { name: '' };
  isLegacy = this.repositoryService.isLegacyEntry.bind(this.repositoryService);
  software$: Observable<IResultList<IManagedObject>> = combineLatest(
    this.textFilter$,
    this.deviceType$
  ).pipe(
    switchMap(([name, deviceType]) => this.getSoftware(name, deviceType)),
    shareReplay(1)
  );
  readonly NO_DEVICE_TYPE_AVAILABLE = gettext('No device type available');
  @ViewChildren(ListItemRadioComponent) radioButtons: QueryList<ListItemRadioComponent>;

  private deviceTypeSubscription: Subscription;

  constructor(
    private repositoryService: RepositoryService,
    private translate: TranslateService
  ) {
    this.loadDeviceTypes();
  }

  @memoize(property('id'))
  getVersions(software: IManagedObject) {
    return this.repositoryService.listAllVersions(software);
  }

  getDeviceTypeTitle(software: IManagedObject): string {
    return get(
      software,
      'c8y_Filter.type',
      this.translate.instant(this.NO_DEVICE_TYPE_AVAILABLE));
  }

  loadDeviceTypes() {
    this.deviceTypeSubscription = this.deviceType$
      .pipe(
        debounceTime(300),
        distinctUntilChanged(),
        switchMap(searchStr => {
          const query = { 'c8y_Filter.type': `*${searchStr}*` };
          return from(
            this.repositoryService.listRepositoryEntries(RepositoryType.SOFTWARE, { query })
          );
        })
      )
      .subscribe(result => {
        const { data } = result;
        this.deviceTypes = uniqWith(data.map(val => ({ name: val.c8y_Filter.type })), isEqual);
      });
  }

  ngOnDestroy() {
    this.deviceTypeSubscription.unsubscribe();
  }

  resetSelection() {
    this.radioButtons.map(radio => radio.reset());
  }

  selectSoftware({ option, software }) {
    software.selectedId = option.id;
    option.action = 'install';
    this.emitSoftware({ option, software });
  }

  emitSoftware({ option, software }) {
    const emitData = Object.assign(
      {},
      { software },
      { version: option },
      { action: option.action }
    );
    this.software.emit(emitData);
  }

  private getSoftware(name?: string, deviceType?: string) {
    const query: any = name ? { name: `*${name}*` } : {};
    if (deviceType) {
      query.__or = [{ 'c8y_Filter.type': deviceType }, { __not: { __has: `c8y_Filter.type` } }];
    }
    return this.repositoryService.listRepositoryEntries(RepositoryType.SOFTWARE, { query });
  }
}
