import { Component, QueryList, ViewChild, ViewChildren } from '@angular/core';
import { IOperationBulk, IResultList } from '@c8y/client';
import { OperationBulkRealtimeService, DatePickerComponent } from '@c8y/ngx-components';
import { flatten } from 'lodash-es';
import { BehaviorSubject, Observable, combineLatest, pipe } from 'rxjs';
import { map, tap, switchMap, withLatestFrom, shareReplay } from 'rxjs/operators';
import { StatusFilterComponent } from '../../status-filter/status-filter.component';
import { IStatusOption } from '../../status-filter/status-option.model';
import { BulkOperationsService } from '../bulk-operations.service';
import { BulkOperationListItemComponent } from './bulk-operation-list-item.component';
import { BULK_OPERATION_STATUS_OPTIONS } from './bulk-operation-list-item.model';
import { OperationType } from './type-list/operation-type.model';

@Component({
  selector: 'c8y-bulk-operations',
  templateUrl: './bulk-operations-list.component.html',
  providers: [OperationBulkRealtimeService]
})
export class BulkOperationsListComponent {
  selectedTypeFilters = this.getTypeFilters();
  bulkOperationStatusOptions: IStatusOption[] = BULK_OPERATION_STATUS_OPTIONS;

  filterPipe;
  refreshLoading: boolean = false;
  statusFilter$: BehaviorSubject<IStatusOption[]> = new BehaviorSubject(null);
  typeFilter$: BehaviorSubject<OperationType[]> = new BehaviorSubject(null);
  timeFilter$: BehaviorSubject<any> = new BehaviorSubject(null);
  reload$: BehaviorSubject<void> = new BehaviorSubject(null);
  @ViewChildren(BulkOperationListItemComponent) listItems: QueryList<
    BulkOperationListItemComponent
  >;
  @ViewChild('statusFilter', { static: true }) statusFilter: StatusFilterComponent;
  @ViewChild(DatePickerComponent, { static: true }) datePicker: DatePickerComponent;

  bulkOperations$: Observable<IResultList<IOperationBulk>> = combineLatest(
    this.statusFilter$,
    this.timeFilter$,
    this.typeFilter$,
    this.reload$
  ).pipe(
    tap(() => {
      this.refreshLoading = true;
    }),
    switchMap(([statusFilters, timeFilters]) => this.filter(statusFilters, timeFilters)),
    withLatestFrom(this.typeFilter$),
    map(([result, typeFilter]: [IResultList<IOperationBulk>, OperationType[]]) => {
      this.filterPipe = pipe(map((data: []) => this.filterByType(data, typeFilter)));
      return { ...result, data: this.filterByType(result.data, typeFilter) };
    }),
    tap(() => {
      this.refreshLoading = false;
    }),
    shareReplay(1)
  );

  private allFilterFragments: string[];

  constructor(
    public realtime: OperationBulkRealtimeService,
    private bulkOperationsService: BulkOperationsService
  ) {
    this.allFilterFragments = this.flattenFilterFragments(this.getTypeFilters());
  }

  filterByType(bulkOperations: IOperationBulk[], typeFilter) {
    const flattenedFragments: string[] = this.flattenFilterFragments(typeFilter);
    if (
      // return data unfiltered if no filters selected...
      !flattenedFragments.length ||
      // ...or when all filters are selected
      this.allFilterFragments.every(fragment => flattenedFragments.includes(fragment))
    ) {
      return bulkOperations;
    }

    const filteredData = bulkOperations.filter(item => {
      return Object.keys(item.operationPrototype).some(key => flattenedFragments.includes(key));
    });

    return filteredData;
  }

  resetFilter() {
    this.statusFilter$.next(null);
    this.timeFilter$.next(null);
    this.typeFilter$.next(null);

    this.datePicker.clearFilter();
    this.selectedTypeFilters = this.getTypeFilters();
    this.statusFilter.statusOptions.map(option => {
      option.selected = false;
      return option;
    });
  }

  isFilterApplied() {
    return (
      this.statusFilter$.getValue() || this.typeFilter$.getValue() || this.timeFilter$.getValue()
    );
  }

  filter(statusFilters, timeFilter) {
    const status =
      statusFilters && statusFilters.length > 0
        ? {
            generalStatus: flatten(statusFilters.map(statusFilter => statusFilter.generalStatus))
          }
        : {};

    const time = timeFilter
      ? {
          ...(timeFilter.dateFrom && {
            dateFrom: timeFilter.dateFrom.toISOString()
          }),
          ...(timeFilter.dateTo && {
            dateTo: timeFilter.dateTo.toISOString()
          })
        }
      : {};
    return this.getBulkOperations({ ...status, ...time });
  }

  getBulkOperations(filter?) {
    return this.bulkOperationsService.getBulkOperations(filter);
  }

  getTypeFilters() {
    return this.bulkOperationsService.getBulkTypes();
  }

  addBulkOperation() {
    this.bulkOperationsService.showNewBulkOperationModal();
  }

  openFailedOperation(failedParentId) {
    this.listItems.forEach(item => {
      if (item.bulkOperation.id === failedParentId) {
        item.listItem.collapsed = false;
        item.listItem.element.nativeElement.scrollIntoView({ behavior: 'smooth', block: 'center' });
      }
    });
  }

  compareOperations(operationA: IOperationBulk, operationB: IOperationBulk): number {
    return new Date(operationA.startDate).getTime() - new Date(operationB.startDate).getTime();
  }

  private flattenFilterFragments(filters: OperationType[]): string[] {
    return (filters || []).reduce((flattened, current) => flattened.concat(current.fragments), []);
  }
}
